body,
.header {
  height: 100%;
  width: 100%;
}

.Login_form {
  background-color: #a8bca1;
  height: 50%;
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.LoginPage {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  outline: 0.4rem blueviolet solid;
  background-color: #94ae89;
}

.LoginPage__Username {
  display: flex;
  width: 20vw;
  justify-content: space-around;
  padding: 2rem;
}

.LoginPage__Username label {
  font-size: 2.5vw;
  padding: 1rem;
}

.LoginPage__Username input {
  padding: 1rem;
  font-size: 2vw;
  border-radius: 20px;
  letter-spacing: 8px;
}

.LoginPage__Email {
  display: flex;
  width: 20vw;
  justify-content: space-around;
  padding: 2rem;
}

.LoginPage__Email label {
  font-size: 2.5vw;
  padding: 1rem;
}

.LoginPage__Email input {
  padding: 1rem;
  font-size: 2vw;
  border-radius: 20px;
  letter-spacing: 8px;
}

.LoginPage__Password {
  display: flex;
  width: 20vw;
  justify-content: space-around;
  padding: 2rem;
}

.LoginPage__Password label {
  font-size: 2.5vw;
  padding: 1rem;
}

.LoginPage__Password input {
  padding: 1rem;
  font-size: 2vw;
  border-radius: 20px;
  letter-spacing: 8px;
}

.LoginPage .Submit_btn {
  padding: 1vh 2vw;
  font-size: 1.8vw;
  margin-left: 10vw;
  border-radius: 20px;
}

.toSignup {
  color: brown;
  font-size: 2rem;
  padding: 2rem 0 0 21%;
}
